"use client";

import { useState } from "react";
import { PlusCircle } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Project, mockProjects, mockClients } from "@/data/mock";
import { DataTable } from "./data-table";
import { columns } from "./columns";
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { useToast } from "@/hooks/use-toast";

const projectFormSchema = z.object({
  projectName: z.string().min(3, "Project name must be at least 3 characters."),
  clientName: z.string().min(1, "Client selection is required."),
  deadline: z.string().refine((val) => !isNaN(Date.parse(val)), {
    message: "Invalid date format",
  }),
  status: z.enum(["In Progress", "Completed", "Pending", "Overdue"]),
  pic: z.string().min(2, "PIC name is required."),
});

type ProjectFormValues = z.infer<typeof projectFormSchema>;

export default function ProjectsPage() {
  const [projects, setProjects] = useState<Project[]>(mockProjects);
  const [isSheetOpen, setIsSheetOpen] = useState(false);
  const { toast } = useToast();

  const addProject = (newProjectData: ProjectFormValues) => {
    const newProject: Project = {
      id: `PROJ${(projects.length + 1).toString().padStart(3, "0")}`,
      ...newProjectData,
    };
    setProjects((prev) => [...prev, newProject]);
    toast({
      title: "Project Added",
      description: `${newProject.projectName} has been successfully added.`,
    });
    setIsSheetOpen(false);
  };

  return (
    <div className="flex flex-col gap-6">
      <div className="flex items-center justify-between">
        <h1 className="font-headline text-3xl font-bold tracking-tight">
          Project Management
        </h1>
        <Sheet open={isSheetOpen} onOpenChange={setIsSheetOpen}>
          <SheetTrigger asChild>
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" />
              Add Project
            </Button>
          </SheetTrigger>
          <SheetContent className="sm:max-w-lg">
            <SheetHeader>
              <SheetTitle>Add New Project</SheetTitle>
              <SheetDescription>
                Fill in the details for the new project.
              </SheetDescription>
            </SheetHeader>
            <ProjectForm onSubmit={addProject} />
          </SheetContent>
        </Sheet>
      </div>
      <DataTable columns={columns} data={projects} />
    </div>
  );
}

function ProjectForm({ onSubmit }: { onSubmit: (data: ProjectFormValues) => void }) {
  const form = useForm<ProjectFormValues>({
    resolver: zodResolver(projectFormSchema),
    defaultValues: {
      deadline: new Date().toISOString().split("T")[0],
      status: "Pending",
    },
  });

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="grid gap-4 py-4">
        <FormField control={form.control} name="projectName" render={({ field }) => (
            <FormItem><FormLabel>Project Name</FormLabel><FormControl><Input {...field} /></FormControl><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="clientName" render={({ field }) => (
            <FormItem><FormLabel>Client</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl><SelectTrigger><SelectValue placeholder="Select a client" /></SelectTrigger></FormControl>
                <SelectContent>
                    {mockClients.map(client => (
                        <SelectItem key={client.id} value={client.company}>{client.company}</SelectItem>
                    ))}
                </SelectContent>
            </Select><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="deadline" render={({ field }) => (
            <FormItem><FormLabel>Deadline</FormLabel><FormControl><Input type="date" {...field} /></FormControl><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="status" render={({ field }) => (
            <FormItem><FormLabel>Status</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl><SelectTrigger><SelectValue /></SelectTrigger></FormControl>
                <SelectContent>
                    <SelectItem value="In Progress">In Progress</SelectItem>
                    <SelectItem value="Completed">Completed</SelectItem>
                    <SelectItem value="Pending">Pending</SelectItem>
                    <SelectItem value="Overdue">Overdue</SelectItem>
                </SelectContent>
            </Select><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="pic" render={({ field }) => (
            <FormItem><FormLabel>Person In Charge (PIC)</FormLabel><FormControl><Input {...field} /></FormControl><FormMessage /></FormItem>
        )} />

        <Button type="submit" className="w-full mt-4">Save Project</Button>
      </form>
    </Form>
  );
}
