"use client";

import { ColumnDef } from "@tanstack/react-table";
import { MoreHorizontal, ArrowUpDown } from "lucide-react";

import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Badge } from "@/components/ui/badge";
import { Project } from "@/data/mock";

export const columns: ColumnDef<Project>[] = [
  {
    accessorKey: "projectName",
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Project Name
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      );
    },
     cell: ({ row }) => (
        <div className="font-medium">{row.getValue("projectName")}</div>
    ),
  },
  {
    accessorKey: "clientName",
    header: "Client",
  },
  {
    accessorKey: "deadline",
    header: "Deadline",
    cell: ({ row }) => new Date(row.getValue("deadline")).toLocaleDateString(),
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ row }) => {
      const status = row.getValue("status") as string;
      const variant: "default" | "secondary" | "destructive" | "outline" =
        status === "In Progress"
          ? "default"
          : status === "Completed"
          ? "secondary"
          : status === "Pending"
          ? "outline"
          : "destructive";
      return <Badge variant={variant} className={status === 'In Progress' ? 'bg-blue-500/80 hover:bg-blue-500 text-white' : status === 'Overdue' ? 'bg-red-500/80 hover:bg-red-500 text-white' : ''}>{status}</Badge>;
    },
  },
  {
    accessorKey: "pic",
    header: "PIC",
  },
  {
    id: "actions",
    cell: ({ row }) => {
      const project = row.original;
      return (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="h-8 w-8 p-0">
              <span className="sr-only">Open menu</span>
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuLabel>Actions</DropdownMenuLabel>
            <DropdownMenuItem>View Details</DropdownMenuItem>
            <DropdownMenuItem>Edit Project</DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      );
    },
  },
];
